import React from 'react';
import { Jumbotron, Spinner, Button } from 'react-bootstrap';
import { useHistory } from 'react-router-dom';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'
import { faHeart } from '@fortawesome/free-solid-svg-icons';
export const Home = ({ allMovies, setSelectedMovieData }) => {
    const Router = useHistory();
    return (
        <>
            <Jumbotron className="mt-4 mx-5">
                <p className="lead my-0">Latest Movies</p>
                <hr className="my-2" />
            </Jumbotron>
            <div className="main">
            <div className="center">
                {
                    allMovies.length <= 0 && (
                        <div align="center">
                            <Spinner animation="grow" />
                        </div>
                    )
                }

                {
                    allMovies.map(movie => (
                        <div key={movie.id} onClick={() => { setSelectedMovieData(movie); Router.push('/movie-details') }}>
                            <div className="results">
                                <div className="card1">
                                    <img src={`https://image.tmdb.org/t/p/w500${movie.poster_path}`} alt={`${movie.title}`} />
                                    <div className="title">
                                        <h6>{movie.title}</h6>
                                        <Button className="btn-warning"><FontAwesomeIcon icon={faHeart} /></Button>
                                    </div>
                                </div>
                            </div>
                        </div>
                    ))
                }
            </div >
            </div>
        </>
    )
}
