import React, { useEffect, useState } from 'react'
import './App.css'
import { BrowserRouter as Router, Route, Switch } from 'react-router-dom';
import Navbar from './components/Navbar';
import Search from './components/Search';
import { Home } from './components/Home';
import Axios from 'axios';
import MovieDetails from './components/MovieDetails';


const App = () => {

    const [allMovies, setAllMovies] = useState([]);
    const [selectedMovieData, setSelectedMovieData] = useState({});


    const fetchMovieData = async () => {
        try {
            const movieDataResponse = await Axios.get(`https://api.themoviedb.org/3/movie/popular?api_key=4e44d9029b1270a757cddc766a1bcb63&language=en-US&page=1`);
            console.log('movieDataResponse: ', movieDataResponse);
            setAllMovies(movieDataResponse.data.results)
        } catch (error) {
            console.log('Error Occured in Fetching Movie Data From API: ', error);
        }
    }

    useEffect(() => {
        fetchMovieData();
    }, [])

    return (
        <>
            <Router>
                <Navbar />
                {/* <Results /> */}
                <Route exact path="/search">
                    <Search />
                </Route>
                <Route exact path="/">
                    <Home allMovies={allMovies} setSelectedMovieData={setSelectedMovieData} />
                </Route>
                <Route exact path="/movie-details">
                    <MovieDetails selectedMovieData={selectedMovieData} />
                </Route>
            </Router>
        </>
    )
}

export default App;