import React from 'react';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'
import { faHeart } from '@fortawesome/free-solid-svg-icons';
import { Button } from 'react-bootstrap';
const MovieDetails = ({ selectedMovieData }) => {
    console.log('selectedMovieData: ', selectedMovieData);
    return (
        <>
            <div className="center">
                <div className="card2 card_responsive ">
                    <div className="card2_left cad">
                        <img src={`https://image.tmdb.org/t/p/w500${selectedMovieData.poster_path}`} alt={`${selectedMovieData.title}`} />

                    </div>
                    <div className="card2_right cad card_responsive">
                        <p>{selectedMovieData.title}</p>
                        <div className="list">
                            
                            <p>{selectedMovieData.release_date}</p>
                            <p>Votes:{selectedMovieData.vote_count}</p>
                        </div>
                        <div className="overview">{selectedMovieData.overview}</div>
                        <Button className="btn-warning"><FontAwesomeIcon icon={faHeart} /> Add Favourite</Button>
                    </div>

                </div>
            </div>
        </>
    )
}

export default MovieDetails